import { Map, List } from 'immutable'

const categories = List([
  Map({
    category: Map({
      name: 'Transporte',
      color: 'orange'
    }),
    items: List()
  }),
  Map({
    category: Map({
      name: 'Educao',
      color: 'blue'
    }),
    items: List()
  }),
  Map({
    category: Map({
      name: 'Segurana pblica',
      color: 'brown'
    }),
    items: List()
  }),
  Map({
    category: Map({
      name: 'Sade',
      color: 'green'
    }),
    items: List()
  }),
  Map({
    category: Map({
      name: 'Outros',
      color: 'gray'
    }),
    items: List()
  })
]);

export default categories;


